.product-item {
  display: -ms-flexbox;
  display: flex;
  -webkit-box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
          box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  height: 12.5rem;
  margin: 1rem 0;
  position: relative;
}

.product-item__image {
  width: 35%;
  height: 100%;
  background-position: center;
  background-size: cover;
  margin-right: 5%;
  background-repeat: no-repeat;
  background-size: contain;
}

.product-item__content {
  width: 60%;
  margin: 0.2rem;
}

.product-item__content h1 {
  font-size: 1.25rem;
  color: #6200ff;
  margin: 0.5rem 0;
}

.product-item__content h2 {
  font-size: 1rem;
  color: #292929;
  margin: 0;
}

.product-item__controls {
  position: absolute;
  bottom: 1rem;
  right: 1rem;
  text-align: right;
}

.product-item__controls button,
.product-item__controls a {
  text-decoration: none;
  cursor: pointer;
  font: inherit;
  border: none;
  background: transparent;
  color: #6200ff;
  padding: 0;
  margin: 0 0.5rem;
}

.product-item__controls button:hover,
.product-item__controls button:active,
.product-item__controls a:hover,
.product-item__controls a:active {
  color: #320083;
}

@media screen and (max-width: 649px) {
  .product-item__content {
    text-align: center;
    font-size: 70%;
  }
  .product-item__controls {
    bottom: 0.5rem;
  }
}



